.high-score {
    width: 100%;
    margin-top: 40px;
    padding: 40px;

    background-color: #fff;
    border: 2px solid #c7c7c7;
    border-radius: 4px;
}

.high-score__logo {
    margin: 0 auto 66px;
    width: 96px;
    height: 96px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.high-score__title {
    margin-bottom: 30px;

    color: #727171;
    font-family: "Gill Sans MT";
    font-size: 48px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.high-score__list {
    margin-bottom: 30px;
}

.high-score__item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &+& {
        margin-top: 20px;
    }
}

.high-score__info {
    position: relative;
    padding-left: 35px;
}

.high-score__num {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 45px;

    border: 4px solid #d0cece;
    border-radius: 50%;

    color: #000000;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;

    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-50%);
}

.high-score__name {
    display: block;

    color: #727171;
    font-family: "Gill Sans MT";
    font-size: 18px;
}

.high-score__date {
    color: #727171;
    font-family: "Gill Sans MT";
    font-weight: 400;
    font-size: 14px;
}

.high-score__time {
    font-weight: 400;
    color: #000;
    font-family: "Gill Sans MT";
    font-size: 18px;
}