.register {
    margin-top: 10px;
    margin-bottom: 20px;
    padding: 40px 30px 30px;

    background-color: #fff;
    border: 2px solid #c7c7c7;
    border-radius: 4px;
}

.register__logo {
    width: 183px;
    height: 91px;
    margin: 0 auto 87px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.register__title {
    margin-bottom: 30px;

    color: #727171;
    font-family: "Gill Sans MT";
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 100%;
    font-size: 48px;

    span {
        color: #727171;
        font-family: "Gill Sans MT";
        font-size: 30px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 100%;
    }
}


.register__input {
    margin-bottom: 10px;
}

.register__btn {
    margin-top: 20px;
}