.scoreboard {
    padding: 20px 30px 40px;
    margin-bottom: 40px;

    background-color: #fff;
    border: 2px solid #c7c7c7;
    border-radius: 4px;
}

.scoreboard__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
}

.scoreboard__title {
    color: #727171;
    font-family: "Gill Sans MT";
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 100%;
}

.scoreboard__link {
    color: #c1bdc5;
    font-family: "Gill Sans MT";
    font-size: 18px;
    font-weight: 400;

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            color: #000;
        }
    }
}

.scoreboard__list {}

.scoreboard__item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &+& {
        margin-top: 20px;
    }
}

.scoreboard__info {
    padding-left: 55px;

    position: relative;
}

.scoreboard__num {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: #e7be07;

    color: #ffffff;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;

    position: absolute;
    top: 0;
    left: 0;
}

.scoreboard__name {
    display: block;

    color: #727171;
    font-family: "Gill Sans MT";
    font-size: 18px;
}

.scoreboard__date {
    color: #727171;
    font-family: "Gill Sans MT";
    font-weight: 400;
    font-size: 14px;
}

.scoreboard__time {
    font-weight: 400;
    color: #000;
    font-family: "Gill Sans MT";
    font-size: 18px;
}