.rules {
    padding: 10px 25px 20px;
    margin-top: 40px;

    background-color: #fff;
    border: 2px solid #c7c7c7;
    border-radius: 4px;
}

.rules__logo {
    width: 81px;
    height: 81px;
    margin: 0 auto 70px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.rules__title {
    margin-bottom: 30px;

    color: #727171;
    font-family: "Gill Sans MT";
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    font-size: 48px;
}

.rules__list {
    padding-left: 20px;
    margin-bottom: 20px;

    li {
        color: #727171;
        font-family: "Gill Sans MT";
        font-size: 30px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        font-size: 24px;
    }
}

