.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 340px 16px 120px;

    background-color: #000000;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    @media(max-width:1439px) {
        padding: 16px 40px;
    }
}

.header__logo {
    display: flex;
    align-items: center;

    user-select: none;

    img {
        display: block;
        width: 36px;
        height: 22px;
        margin-right: 30px;
        object-fit: contain;
    }

    span {
        color: #ffffff;
        font-family: "Gill Sans MT";
        font-size: 30px;
        font-weight: 400;
        line-height: normal;
    }
}

.number {
    display: flex;
    align-items: center;
}

.number__text {
    margin-right: 30px;

    color: #ffffff;
    font-family: "Gill Sans MT";
    font-size: 30px;
    font-weight: 400;
    line-height: normal;
}

.number__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 32px;

    border-radius: 5px;

    color: #ffffff;
    font-family: "Gill Sans MT";
    font-size: 18px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-transform: uppercase;

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            opacity: .7;
        }
    }

    &--one {
        background-color: #e7be07;
        margin-right: 30px;
    }

    &--two {
        background-color: #f93636;
        margin-right: 30px;
    }

    &--three {
        background-color: #f93636;
    }
}

.header__nav {
    display: flex;
    align-items: center;
}

.timer {
    padding-left: 51px;
    margin-right: 36px;

    color: #ffffff;
    font-family: "Gill Sans MT";
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;

    position: relative;

    &::before {
        content: '';
        width: 29px;
        height: 32px;

        background-image: url('../img/timer.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }
}

.escape {
    display: flex;

    padding: 4px 26px 4px 19px;

    border-radius: 5px;
    background-color: #e7be07;

    color: #000000;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            opacity: .7;
        }
    }

    img {
        width: 30px;
        height: 29px;
        display: block;
        margin-right: 17px;

        object-fit: contain;
    }
}