* {
    font-family: "Gill Sans MT", sans-serif;
}

body {
    background: #fff;
}

.container {
    width: 100%;
    max-width: 510px;
    margin: 0 auto;
    padding: 0 15px;
}

.page {
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    min-height: 100vh;

    background-image: url('../img/bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.background {
    display: block;
    width: 100%;
    height: 100vh;
}

.nav {
    display: flex;
    flex-direction: column;

    position: absolute;
    top: 190px;
    left: 130px;
}

.nav__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 52px;

    border-radius: 5px;

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            transform: scale(1.1);
        }
    }

    &+& {
        margin-top: 20px;
    }

    &--one {
        background-color: #000000;
    }

    &--two {
        background-color: #000000;
    }

    &--three {
        background-color: #000000;
    }

    &--four {
        background-color: #f93636;
    }
}

.input {
    display: block;
    width: 100%;
    padding: 15px 10px;

    border-radius: 5px;
    border: 1px solid #c1bdc5;

    color: #c1bdc5;
    font-family: "Gill Sans MT";
    font-size: 21px;
    font-weight: 400;

    &::placeholder {
        color: #c1bdc5;
        font-family: "Gill Sans MT";
        font-size: 21px;
        font-weight: 400;
    }
}

.top-nav {
    display: flex;

    position: absolute;
    top: 20px;
    right: 60px;
}

.link {
    display: block;
    width: 166px;
    padding: 14px;

    box-shadow: 0 3px 0 rgba(9, 9, 9, 0.24);
    border-radius: 5px;
    background-color: #ffffff;

    color: #000000;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;

    &+& {
        margin-left: 15px;
    }

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            box-shadow: none;
            transform: translateY(2px);
        }
    }
}

.btn {
    display: block;
    width: 100%;
    padding: 5px;
    border-radius: 5px;

    background-color: #e7be07;

    color: #000000;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            background-color: #000000;
            color: #fff;
        }
    }

    &--dark {
        background-color: #000000;
        color: #fff;

        @media(any-hover: hover) {
            &:hover {
                background-color: #e7be07;
                color: #000000;
            }
        }
    }

    &--red {
        background-color: #f93636;
    }
}

.popup {
   display: flex;
   justify-content: center;
   align-items: flex-start;
   padding-top: 40px;
   padding-bottom: 40px;

    overflow-y: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
    background-color: rgba(#000, .65);
    opacity: 0;
    visibility: hidden;

    position: fixed;
    top: 0;
    left:0;
    right: 0;
    bottom: 0;
    z-index: 10;
    transition: .2s linear;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.popup__btn {
    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 100%;
    text-align: center;

    position: absolute;
    top: 11px;
    right: 16px;

    transition: .2s linear;

    @media(any-hover: hover) {
        &:hover {
            color: #000;
        }
    }
}

.popup__inner {
    width: 673px;
    padding: 60px 20px 35px;

    border-radius: 5px;
    border: 2px solid #f0f0f0;
    background-color: #ffffff;

    position: relative;

    &--answers {
        width: 934px;
    }
}

.item {
    // background-color: red;
    position: absolute;
}