.puzzle__img {
    width: 81px;
    height: 99px;
    margin: 0 auto 24px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.puzzle__title {
    margin-bottom: 30px;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.puzzle__text {
    margin-bottom: 90px;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 30px;
    font-style: italic;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.puzzle__wrapper {
    margin-bottom: 60px;
}

.puzzle__input {
    width: 69px;
    height: 65px;
    padding: 5px;

    border-radius: 5px;
    border: 4px solid #f0f0f0;
    background-color: #ffffff;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    line-height: 100%;
    text-transform: uppercase;
    text-align: center;

    &+& {
        margin-left: 19px;
    }
}

.puzzle__btn {
    padding: 20px 0;
    width: 270px;
    margin: 0 auto;
}