.answers__img {
    width: 92px;
    height: 87px;
    margin: 0 auto 22px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.answers__title {
    max-width: 492px;
    margin: 0 auto 25px;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.answers__descr {
    max-width: 560px;
    margin: 0 auto 25px;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 30px;
    font-style: italic;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.answers__list {
    margin-bottom: 60px;
}

.answers__group {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;

    &+& {
        margin-top: 34px;
    }
}

.answers__input {
    display: block;
    width: 100%;
    height: 65px;
    padding: 5px;

    border-radius: 5px;
    border: 4px solid #f0f0f0;
    background-color: #ffffff;

    color: #000;
    font-family: "Gill Sans MT";
    font-size: 24px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-transform: uppercase;

    transition: .2s linear;

    &::placeholder {
        color: #cbcaca;
        text-transform: capitalize;
    }

    &:focus {
        border: 4px solid #adadad;
    }
}

.answers__nav {
    display: flex;
    justify-content: space-around;

    .btn {
        width: 270px;
        padding: 20px;
    }
}


