.congratulations__img {
    height: 285px;
    margin-bottom: 52px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.congratulations__title {
    margin-bottom: 36px;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.congratulations__list {
    margin-bottom: 30px;
}

.congratulations__item {
    display: flex;
    justify-content: center;

    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 36px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;

    span {
        margin-left: 5px;

        color: #736c7c;
        font-family: "Gill Sans MT - Bold";
        font-size: 36px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
    }
}

.congratulations__text {
    margin-bottom: 20px;

    font-size: 30px;
    color: #736c7c;
    font-weight: 300;
    font-family: "Gill Sans MT - Italic";
    font-style: normal;
    letter-spacing: normal;
}

.congratulations__nav {
    display: flex;
    justify-content: space-between;

    .btn {
        width: calc(50% - 22px);
        padding: 20px;
    }
}