.editor__img {
    width: 87px;
    height: 87px;
    margin: 0 auto 52px;
}

.editor__title {
    margin-bottom: 40px;
    color: #736c7c;
    font-family: "Gill Sans MT - Bold";
    font-size: 48px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
}

.editor__text {
    margin-bottom: 20px;

    color: #736c7c;
    font-size: 24px;
    font-style: italic;
    letter-spacing: normal;
    line-height: normal;
    font-family: "Gill Sans MT - Italic";

}

.editor__descr {
    color: #736c7c;
    font-family: "Gill Sans MT";
    font-size: 24px;
    letter-spacing: normal;
    line-height: normal;

    span {
        display: block;

        color: #736c7c;
        font-size: 24px;
        font-style: italic;
        letter-spacing: normal;
        line-height: normal;
        font-family: "Gill Sans MT - Italic";
    }
}